'use client';

import React, { useState, useEffect } from 'react';
// import { useRouter } from 'next/navigation';  // Using Next.js router

interface Contact {
  id: number;
  name: string;
  phone: string;
  email: string;
}

const ContactsPage = () => {
  const [contacts, setContacts] = useState<Contact[]>([]);
  const [newContact, setNewContact] = useState({ name: '', phone: '', email: '' });
  const [editingContact, setEditingContact] = useState<Contact | null>(null); // State for editing a contact
//   const router = useRouter();

  // Fetch contacts from the backend
  useEffect(() => {
    fetch('http://localhost:5000/api/contacts')
      .then((res) => res.json())
      .then((data) => setContacts(data))
      .catch((err) => console.error('Error fetching contacts:', err));
  }, []);

  // Handle form input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    if (editingContact) {
      setEditingContact((prev) => ({ ...prev!, [name]: value }));
    } else {
      setNewContact((prev) => ({ ...prev, [name]: value }));
    }
  };

  // Handle form submission for creating a new contact
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (editingContact) {
      await updateContact(editingContact);
    } else {
      await createContact(newContact);
    }
  };

  // Create a new contact
  const createContact = async (contact: { name: string; phone: string; email: string }) => {
    try {
      const response = await fetch('http://localhost:5000/api/contacts', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(contact),
      });

      if (!response.ok) {
        throw new Error('Failed to create contact');
      }

      const createdContact = await response.json();
      setContacts((prev) => [...prev, createdContact]); // Update the list with the new contact
      setNewContact({ name: '', phone: '', email: '' }); // Reset the form
    } catch (error) {
      console.error(error);
    }
  };

  // Update an existing contact
  const updateContact = async (contact: Contact) => {
    try {
      const response = await fetch(`http://localhost:5000/api/contacts/${contact.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(contact),
      });

      if (!response.ok) {
        throw new Error('Failed to update contact');
      }

      const updatedContact = await response.json();
      setContacts((prev) => prev.map((c) => (c.id === updatedContact.id ? updatedContact : c)));
      setEditingContact(null); // Reset the editing state
    } catch (error) {
      console.error(error);
    }
  };

  // Handle edit button click
  const handleEdit = (contact: Contact) => {
    setEditingContact(contact);
    setNewContact({ name: contact.name, phone: contact.phone, email: contact.email });
  };

  return (
    <div>
      <h1>Contact Management</h1>
      <p>Welcome to the Contact Management System</p>

      {/* Form to create or update a contact */}
      <form onSubmit={handleSubmit}>
        <h2>{editingContact ? 'Edit Contact' : 'Create New Contact'}</h2>
        <div>
          <label>
            Name:
            <input
              type="text"
              name="name"
              value={editingContact ? editingContact.name : newContact.name}
              onChange={handleInputChange}
              required
            />
          </label>
        </div>
        <div>
          <label>
            Phone:
            <input
              type="text"
              name="phone"
              value={editingContact ? editingContact.phone : newContact.phone}
              onChange={handleInputChange}
              required
            />
          </label>
        </div>
        <div>
          <label>
            Email:
            <input
              type="email"
              name="email"
              value={editingContact ? editingContact.email : newContact.email}
              onChange={handleInputChange}
              required
            />
          </label>
        </div>
        <button type="submit">{editingContact ? 'Update Contact' : 'Add Contact'}</button>
      </form>

      {/* Display the list of contacts */}
      <h2>Contacts</h2>
      {contacts.length > 0 ? (
        <ul>
          {contacts.map((contact) => (
            <li key={contact.id}>
              {contact.name} - {contact.phone} - {contact.email}
              <button onClick={() => handleEdit(contact)}>Edit</button>
            </li>
          ))}
        </ul>
      ) : (
        <p>No contacts available.</p>
      )}
    </div>
  );
};

export default ContactsPage;
